.experience{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5rem;
    margin-top: 0;
    height: 30vh;
    margin-bottom:28rem;
    margin-top: -50px;
    

}

.achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.circle{
    width: 4rem;
    height: 4rem;
    background:white ;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
}
.circle::before{
    border-radius: 100%;
    content: "";
    background-image: linear-gradient(to bottom, #87e6fb 0%, #ffc05c 100%);
    top: -8px;left: -8px;bottom: -8px; right: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: 0px 20px 24px 3px rgba(150, 104, 45, 0.42);
}
.achievement span:nth-of-type(2){
    color: orange;
}

.achievement span:nth-of-type(3){
    color: rgb(100, 99, 97);
}